import React, { memo } from "react";
import { ShowsCategoryMobileWrapper } from "./style";
import KTClassifyHeader from "@/components/classify/classify-header";
import KTListViewHorizontal from "@/components/scroll-view/list-view-horizontal";
import KTSpace from "@/components/space";
import KTShowsCategoryCell from "../shows-category-cell";

const KTShowsCategoryMobile = memo(({ categorys, windowWidth }) => {
  return (
    <ShowsCategoryMobileWrapper>
      <KTClassifyHeader
        title="Genres"
        windowWidth={windowWidth}
        hiddenViewAll={true}
      />
      <KTSpace height={windowWidth > 480 ? "30px" : "30rem"} />
      <KTListViewHorizontal
        items={categorys}
        windowWidth={windowWidth}
        spaceBetween={"30rem"}
        swiperPadding={`14rem 20rem`}
        cellOfItem={(item) => {
          return <KTShowsCategoryCell item={item} />;
        }}
      />
    </ShowsCategoryMobileWrapper>
  );
});

export default KTShowsCategoryMobile;
