import React, { memo } from "react";
import {
  FilterCategoriesHeaderWrapper,
  FilterCategoriesListWrapper,
  FilterCategoriesWrapper,
} from "./style";
import { homeAllFilterCategories } from "@/common/ephemeral-data";
import { useState } from "react";

const KTFilterCategories = memo(() => {
  const [selectedCategories, setSelectedCategories] = useState(
    homeAllFilterCategories.map((item) => item.title)
  );

  function onclickCategories(item, index) {
    if (index === 0) {
      // All
      if (selectedCategories.length === homeAllFilterCategories.length) {
        setSelectedCategories([]);
      } else {
        setSelectedCategories(
          homeAllFilterCategories.map((item) => item.title)
        );
      }
    } else {
      if (selectedCategories.includes(item.title)) {
        setSelectedCategories(
          selectedCategories.filter(
            (title) =>
              title !== item.title && title !== homeAllFilterCategories[0].title
          )
        );
      } else {
        if (selectedCategories.length === homeAllFilterCategories.length - 2) {
          setSelectedCategories([
            homeAllFilterCategories[0].title,
            item.title,
            ...selectedCategories,
          ]);
        } else {
          setSelectedCategories([item.title, ...selectedCategories]);
        }
      }
    }
  }

  function categproesChile() {
    return homeAllFilterCategories.map((item, index) => {
      const selected = selectedCategories.includes(item.title);
      return (
        <div
          className="item"
          style={{
            backgroundImage:
              "url(" +
              require(`../../../../../../../../assets/img/temporary/${
                selected ? item.iconNameSelected : item.iconNameNormal
              }`) +
              ")",
          }}
          onClick={() => onclickCategories(item, index)}
        />
      );
    });
  }

  return (
    <FilterCategoriesWrapper>
      <FilterCategoriesHeaderWrapper>
        <div className="title">Categories</div>
        <div className="divider" />
      </FilterCategoriesHeaderWrapper>
      <FilterCategoriesListWrapper>
        {categproesChile()}
      </FilterCategoriesListWrapper>
    </FilterCategoriesWrapper>
  );
});

export default KTFilterCategories;
