import React, { memo } from "react";
import {
  FilterContentVenueCellTitleWrapper,
  FilterContentVenueCellWrapper,
} from "./style";
import { ReactComponent as CheckBox } from "@/assets/img/icons/icon-checkbox.svg";
import { ReactComponent as CheckBoxS } from "@/assets/img/icons/icon-checkbox-s.svg";

const KTFilterContentVenueCell = memo(
  ({ venue, isSelected = false, onClick }) => {
    return (
      <FilterContentVenueCellWrapper onClick={() => onClick(venue)}>
        <FilterContentVenueCellTitleWrapper>
          {venue.venueName}
        </FilterContentVenueCellTitleWrapper>
        {isSelected ? <CheckBoxS /> : <CheckBox />}
      </FilterContentVenueCellWrapper>
    );
  }
);

export default KTFilterContentVenueCell;
