import React, { memo, useState } from "react";

import { MusicsWrapper } from "./style";
import KTMusicsHeader from "./c-components/musics-header";
import { musicCategory } from "../../common/music-ephemeral-data";
import KTMusicsCategory from "./c-components/musics-category";
import withWindowSize from "@/components/higher-order/withWindowSize";
import KTEventsListView from "@/components/event/events-list-view";
import KTMobileEventList from "@/components/mobile-components/mobile-event-list";
import { LAYOUT_MODE_CARD } from "@/common/constants";
import { homeContentEventItems } from "../../common/ephemeral-data";
import { currValue, currValue3 } from "@/utils";
import KTMusicsFilterBar from "./c-components/musics-filter-bar";
import KTMusicsCategoryMobile from "./c-components/musics-category-mobile";
import KTLayoutModeBar from "../../components/layout-mode-bar";
import { Drawer } from "antd";
import KTMusicFilterContent from "./c-components/musics-filter-content";

const KTMusics = memo(({ windowSize }) => {
  const [layoutMode, setLayoutMode] = useState(LAYOUT_MODE_CARD);
  const [showFilterCotnentView, setShowFilterCotnentView] = useState(false);

  const windowWidth = windowSize.width;
  return (
    <MusicsWrapper>
      <KTMusicsHeader />
      {windowWidth > 480 ? (
        <KTMusicsCategory categorys={musicCategory} windowWidth={windowWidth} />
      ) : (
        <KTMusicsCategoryMobile
          categorys={musicCategory}
          windowWidth={windowWidth}
        />
      )}

      <KTMusicsFilterBar
        windowWidth={windowWidth}
        style={{
          margin:
            windowWidth > 480
              ? `40px ${currValue3(24, 50, 100, windowWidth)} 0px`
              : `20rem 20rem 0px`,
        }}
        onClickFilterItem={() => setShowFilterCotnentView(true)}
      />
      <KTLayoutModeBar
        windowWidth={windowWidth}
        layoutMode={layoutMode}
        didChangeLayoutMode={setLayoutMode}
        showAddressInfo={windowWidth <= 480}
        address={"Overland Park, KS"}
      />
      {windowWidth > 480 ? (
        <KTEventsListView
          style={{ margin: `18px ${currValue(50, 100, windowWidth)}` }}
          key={layoutMode}
          events={homeContentEventItems}
          layoutMode={layoutMode}
          cardWidth={currValue(350, 400, windowWidth)}
          windowWidth={windowWidth}
        />
      ) : (
        <KTMobileEventList
          events={homeContentEventItems}
          layoutMode={layoutMode}
        />
      )}

      <Drawer
        closeIcon={null}
        placement="bottom"
        height={
          windowSize.width > 480
            ? `${window.innerHeight - 80}px`
            : `${window.innerHeight - 59}rem`
        }
        open={showFilterCotnentView}
        onClose={() => setShowFilterCotnentView(false)}
        style={{
          borderRadius:
            windowSize.width > 480 ? "20px 20px 0 0" : "20rem 20rem 0 0",
          background: "#FFF",
        }}
        bodyStyle={{ padding: "0" }}
      >
        <KTMusicFilterContent
          onClose={() => setShowFilterCotnentView(false)}
          windowWidth={windowWidth}
        />
      </Drawer>
    </MusicsWrapper>
  );
});

export default withWindowSize(KTMusics);
