import React, { memo } from "react";

import { AppFooterWrapper } from "./style";
import KTFooterBottom from "./c-components/app-footer-bottom";
import KTFooterTop from "./c-components/app-footer-top";
import withWindowSize from "../higher-order/withWindowSize";

const KTAppFooter = memo(({ windowSize }) => {
  function calculateSize(windowSize) {
    const windowWidth = windowSize.width;
    if (windowWidth >= 1920) {
      return {
        wrapperPaddingLeftRight: "100px",
        linePadding: "100px",
      };
    } else if (windowWidth <= 700) {
      return {
        wrapperPaddingLeftRight: "40px",
        linePadding: "0px",
      };
    } else {
      const ratio = (windowWidth - 700) / (1920 - 700);
      const wrapperPaddingLeftRight = ratio * 60 + 40;
      const linePadding = ratio * 100;
      return {
        wrapperPaddingLeftRight: `${wrapperPaddingLeftRight}px`,
        linePadding: `${linePadding}px`,
      };
    }
  }

  const { wrapperPaddingLeftRight, linePadding } = calculateSize(windowSize);

  return (
    <AppFooterWrapper>
      {windowSize.width > 480
        ? [
            <div
              className="partingline"
              style={{ margin: `0 ${linePadding}` }}
            ></div>,
            <KTFooterTop
              className="top"
              style={{ padding: `0 ${wrapperPaddingLeftRight}` }}
              windowSize={windowSize}
            ></KTFooterTop>,
          ]
        : []}

      <div className="partingline" style={{ margin: `0 ${linePadding}` }}></div>
      <KTFooterBottom
        className="bottom"
        windowSize={windowSize}
        style={{ padding: `0 ${wrapperPaddingLeftRight}` }}
      ></KTFooterBottom>
    </AppFooterWrapper>
  );
});

export default withWindowSize(KTAppFooter);
