import React, { memo } from "react";
import {
  KTLayoutModeBarLeftWrapper,
  KTLayoutModeBarRightWrapper,
  KTLayoutModeBarWrapper,
} from "./style";

import { LAYOUT_MODE_CARD, LAYOUT_MODE_LIST } from "@/common/constants";
import { ReactComponent as CardLayoutIcon } from "@/assets/img/svg/icon-card-layout.svg";
import { ReactComponent as ListLayoutIcon } from "@/assets/img/svg/icon-list-layout.svg";
import { ReactComponent as AddressIcon } from "@/assets/img/svg/icon-address.svg";
import classNames from "classnames";

const KTLayoutModeBar = memo(
  ({
    windowWidth,
    layoutMode,
    didChangeLayoutMode,
    showAddressInfo = false,
    address,
    onClickAddressItem,
  }) => {
    return (
      <KTLayoutModeBarWrapper>
        <KTLayoutModeBarLeftWrapper>
          {showAddressInfo
            ? [
                <AddressIcon className="addressIcon" />,
                <div className="addressTitle" onClick={onClickAddressItem}>
                  {address}
                </div>,
              ]
            : null}
        </KTLayoutModeBarLeftWrapper>
        <KTLayoutModeBarRightWrapper windowWidth={windowWidth}>
          {windowWidth > 480
            ? [
                <CardLayoutIcon
                  className={classNames("layoutModel", {
                    layoutModelActive: layoutMode === LAYOUT_MODE_CARD,
                  })}
                  onClick={() => didChangeLayoutMode(LAYOUT_MODE_CARD)}
                />,
                <ListLayoutIcon
                  className={classNames("layoutModel", {
                    layoutModelActive: layoutMode === LAYOUT_MODE_LIST,
                  })}
                  onClick={() => didChangeLayoutMode(LAYOUT_MODE_LIST)}
                />,
              ]
            : layoutMode === LAYOUT_MODE_LIST
            ? [
                <ListLayoutIcon
                  className="layoutModel layoutModelActive"
                  onClick={() => didChangeLayoutMode(LAYOUT_MODE_CARD)}
                />,
              ]
            : [
                <CardLayoutIcon
                  className="layoutModel layoutModelActive"
                  onClick={() => didChangeLayoutMode(LAYOUT_MODE_LIST)}
                />,
              ]}
        </KTLayoutModeBarRightWrapper>
      </KTLayoutModeBarWrapper>
    );
  }
);

export default KTLayoutModeBar;
