import React, { memo } from "react";
import { AppFooterTopListWrapper } from "./style";

const KTAppFooterTopList = memo(({ title, links }) => {
  const items = links.map((item) => {
    return (
      <div key={item.title} className="item">
        {item.title}
      </div>
    );
  });

  return (
    <AppFooterTopListWrapper>
      <div className="title">{title}</div>
      {items}
    </AppFooterTopListWrapper>
  );
});

export default KTAppFooterTopList;
