import React, { memo, useState } from "react";
import { FilterContentVenueWrapper } from "./style";
import KTFilterCotnentVenueSearchCell from "./c-components/filter-contetn-venue-search-cell";
import KTFilterContentVenueCell from "./c-components/filter-content-venue-cell";

const KTFilterContentVenue = memo(({ venues }) => {
  const [selectedVenueIds, setSelectedVenueIds] = useState([]);

  function onClickVenueCell(item) {
    if (selectedVenueIds.includes(item.id)) {
      setSelectedVenueIds(
        selectedVenueIds.filter((element) => element !== item.id)
      );
    } else {
      setSelectedVenueIds([item.id, ...selectedVenueIds]);
    }
  }

  function onClickSelectAll() {
    const isSelectedAll = venues.length === selectedVenueIds.length;
    if (isSelectedAll) {
      setSelectedVenueIds([]);
    } else {
      setSelectedVenueIds(venues.map((element) => element.id));
    }
  }

  const cells = venues.map((item, index) => {
    return (
      <KTFilterContentVenueCell
        venue={item}
        key={index}
        isSelected={selectedVenueIds.includes(item.id)}
        onClick={onClickVenueCell}
      />
    );
  });

  const isSelectedAll = venues.length === selectedVenueIds.length;

  return (
    <FilterContentVenueWrapper>
      <div className="title">Venue</div>
      <div className="titleBottomLine" />
      <KTFilterCotnentVenueSearchCell />
      <KTFilterContentVenueCell
        venue={{ id: "0", venueName: "Select All" }}
        key={0}
        isSelected={isSelectedAll}
        onClick={onClickSelectAll}
      />
      {cells}
    </FilterContentVenueWrapper>
  );
});

export default KTFilterContentVenue;
