import React, { memo, useState } from "react";
import {
  CategoryItemWrapper,
  ShowsCategoryListWrapper,
  ShowsCategoryWrapper,
  SelectAllWrapper,
} from "./style";

import { ReactComponent as SelectedMark } from "@/assets/img/icons/icon-selected-mark.svg";
import { ReactComponent as UnselectedMark } from "@/assets/img/icons/icon-unselected-mark.svg";
import { currValue2, currValue3 } from "@/utils";
import classNames from "classnames";
import { ReactSVG } from "react-svg";

const KTShowsCategory = memo(({ categorys, windowWidth }) => {
  const [selectedCategorys, setSelectedCategorys] = useState([]);

  function onclickCategoryItem(item) {
    if (selectedCategorys.includes(item.name)) {
      // 已选中
      setSelectedCategorys(
        selectedCategorys.filter((element) => element !== item.name)
      );
    } else {
      setSelectedCategorys([...selectedCategorys, item.name]);
    }
  }

  function onclickSelectAllItem() {
    if (selectedCategorys.length === categorys.length) {
      // 已选中
      setSelectedCategorys([]);
    } else {
      setSelectedCategorys(categorys.map((item) => item.name));
    }
  }

  const items = categorys.map((item) => {
    return (
      <CategoryItemWrapper onClick={() => onclickCategoryItem(item)}>
        <ReactSVG
          src={require(`@/assets/img/temporary/${item.icon}`)}
          style={{
            stroke: selectedCategorys.includes(item.name)
              ? "#4D4D4D"
              : "#B3B3B3",
            "stroke-width": "1.5",
            width: currValue2(50, 60, windowWidth),
            height: currValue2(50, 60, windowWidth),
          }}
        />
        <div
          className={classNames("name", {
            nameActive: selectedCategorys.includes(item.name),
          })}
        >
          {item.name}
        </div>
        {selectedCategorys.includes(item.name) ? (
          <SelectedMark className="selectedMark" />
        ) : (
          <UnselectedMark className="selectedMark" />
        )}
      </CategoryItemWrapper>
    );
  });

  return (
    <ShowsCategoryWrapper
      style={{
        margin: `0 ${currValue3(0, 24, 60, windowWidth)}`,
        marginBottom: currValue3(20, 40, 40, windowWidth),
      }}
    >
      <SelectAllWrapper>
        <div
          className="selectAll"
          style={{
            padding: "40px 50px",
          }}
          onClick={() => onclickSelectAllItem()}
        >
          {selectedCategorys.length === categorys.length
            ? "Unselect All"
            : "Select All"}
        </div>
      </SelectAllWrapper>
      <ShowsCategoryListWrapper
        style={{
          padding: `0 ${currValue3(24, 62, 100, windowWidth)}`,
          paddingBottom: currValue3(24, 60, 70, windowWidth),
          columnGap: currValue2(50, 91, windowWidth),
          rowGap: currValue3(40, 50, 64, windowWidth),
        }}
      >
        {items}
      </ShowsCategoryListWrapper>
    </ShowsCategoryWrapper>
  );
});

export default KTShowsCategory;
