import React, { memo, useState } from "react";
import { SportsCategoryMobileWrapper } from "./style";
import KTClassifyHeader from "@/components/classify/classify-header";
import KTListViewHorizontal from "@/components/scroll-view/list-view-horizontal";
import KTSpace from "@/components/space";
import KTSportsCategoryCell from "../sports-category-cell";

const KTSportsCategoryMobile = memo(({ categorys, windowWidth }) => {
  const [selectedCategorys, setSelectedCategorys] = useState([]);

  function onClickCategoryCell(item) {
    if (item.name === "All") {
      onclickSelectAllItem();
      return;
    }

    if (selectedCategorys.includes(item.name)) {
      // 已选中
      setSelectedCategorys(
        selectedCategorys.filter((element) => element !== item.name)
      );
    } else {
      setSelectedCategorys([...selectedCategorys, item.name]);
    }
  }

  function onclickSelectAllItem() {
    if (selectedCategorys.length === categorys.length) {
      // 已选中
      setSelectedCategorys([]);
    } else {
      setSelectedCategorys(categorys.map((item) => item.name));
    }
  }

  return (
    <SportsCategoryMobileWrapper>
      <KTClassifyHeader
        title="Categories"
        windowWidth={windowWidth}
        hiddenViewAll={true}
      />
      <KTSpace height={windowWidth > 480 ? "30px" : "30rem"} />
      <KTListViewHorizontal
        items={categorys}
        windowWidth={windowWidth}
        spaceBetween={"30rem"}
        swiperPadding={`14rem 20rem`}
        cellOfItem={(item) => {
          return (
            <KTSportsCategoryCell
              item={item}
              isSelected={selectedCategorys.includes(item.name)}
              onClick={onClickCategoryCell}
            />
          );
        }}
      />
    </SportsCategoryMobileWrapper>
  );
});

export default KTSportsCategoryMobile;
