import React, { memo } from "react";
import { Input } from "antd";

import { AppFooterTopLeftWrapper } from "./style";
import { appFooterFollowItems } from "@/common/local-data";

const KTAppFooterTopLeft = memo(({ windowSize }) => {
  const followItems = appFooterFollowItems.map((item) => {
    return (
      <img
        className="followIcon"
        src={require("@/assets/img/icons/" + item.iconName)}
        alt={item.alt}
      />
    );
  });
  let sendMessageBar = null;
  if (windowSize.width > 1024) {
    sendMessageBar = (
      <div className="sendMessageBar">
        <Input
          className="sendInput"
          placeholder="Send me the latest events and special offers!"
          bordered={false}
          style={{
            fontSize: "14px",
            textAlign: "center",
            fontStyle: "normal",
            fontWeight: "400",
            fontFamily: "Inter",
            color: "#797979",
          }}
        />
        <div className="sendButton">
          <img src={require("@/assets/img/icons/icon-send.png")} alt="" />
        </div>
      </div>
    );
  }
  return (
    <AppFooterTopLeftWrapper>
      <div className="follow">
        <div className="followTitlle">FOLLOW OUR SOCIAL MEDIA</div>
        <div className="followIconWrapper">{followItems}</div>
      </div>
      {sendMessageBar}
    </AppFooterTopLeftWrapper>
  );
});

export default KTAppFooterTopLeft;
