import { styled } from "styled-components";

export const EventCellWrapper = styled.div.attrs({
  cWidth: (currWidth) => {
    if (currWidth > 1920) {
      return {
        wrapperMargin: "0 328px",
      };
    } else if (currWidth <= 1224) {
      return {
        wrapperMargin: "0 0",
      };
    } else {
      const ratio = (currWidth - 1224) / (1920 - 1224);
      const wrapperMargin = ratio * 328;
      return {
        wrapperMargin: `0 ${wrapperMargin}px`,
      };
    }
  },
})`
  display: flex;

  justify-content: space-between;
  align-items: center;
  border-bottom: 4px solid #f2f2f2;
  background: #fff;
  margin: ${(props) => props.cWidth(props.windowWidth).wrapperMargin};
`;

export const EventCellLeftWrapper = styled.div`
  display: flex;
  padding: 31px 0;
  justify-content: start;
  align-items: center;
`;

export const EventCellRightWrapper = styled.div`
  margin: 0 40px;
  display: flex;
  justify-content: end;
  align-items: center;
  gap: 30px;
`;
