import React, { memo } from "react";
import { ShowsCategoryCellWrapper } from "./style";
import { ReactSVG } from "react-svg";
import classNames from "classnames";
const KTShowsCategoryCell = memo(({ item, isSelected = false }) => {
  return (
    <ShowsCategoryCellWrapper>
      <ReactSVG
        src={require(`@/assets/img/temporary/${item.icon}`)}
        style={{
          stroke: isSelected ? "#4D4D4D" : "#B3B3B3",
          "stroke-width": "1",
          width: "60px",
          height: "60px",
        }}
      />
      <div
        className={classNames("name", {
          nameActive: isSelected,
        })}
      >
        {item.name}
      </div>
      <div
        className={classNames("bottomLine", {
          bottomLineActive: isSelected,
        })}
      />
    </ShowsCategoryCellWrapper>
  );
});

export default KTShowsCategoryCell;
