import React, { memo, useState } from "react";
import {
  ClassifySubHeaderSlideWrapper,
  ClassifySubHeaderWrapper,
} from "./style";
import { Swiper, SwiperSlide } from "swiper/react";
import classNames from "classnames";
import { currValue } from "../../../utils";

const KTClassifySubHeader = memo(
  ({ items, windowWidth = window.screen.width }) => {
    const [selectedItem, setSelectedItem] = useState(items[0]);

    const swiperSlides = items.map((item) => {
      return (
        <SwiperSlide
          className="swiperSlide"
          onClick={() => setSelectedItem(item)}
        >
          <ClassifySubHeaderSlideWrapper>
            <div
              className={classNames("title", {
                titleSelected: item.title === selectedItem.title,
              })}
            >
              {item.title}
            </div>
            <div className="emoji">
              {item.title === selectedItem.title ? item.emoji : null}
            </div>
          </ClassifySubHeaderSlideWrapper>
        </SwiperSlide>
      );
    });

    return (
      <ClassifySubHeaderWrapper
        style={{
          padding:
            windowWidth > 480
              ? "0 " + currValue(50, 100, windowWidth)
              : "0 20rem",
        }}
      >
        <Swiper
          slidesPerView={"auto"}
          spaceBetween={windowWidth > 480 ? "60px" : "40rem"}
        >
          {swiperSlides}
        </Swiper>
      </ClassifySubHeaderWrapper>
    );
  }
);

export default KTClassifySubHeader;
