import React, { memo } from "react";
import {
  LeftContentWrapper,
  MusicsFilterBarWrapper,
  RightContentWrapper,
} from "./style";
import { currValue2 } from "@/utils";
import KTFilterBaritem from "@/components/filter/filter-baritem";
import KTFilterButton from "@/components/filter/filter-button";

const KTMusicsFilterBar = memo(
  ({ style = {}, windowWidth, onClickFilterItem }) => {
    return (
      <MusicsFilterBarWrapper style={{ ...style }}>
        <LeftContentWrapper
          style={{
            gap: windowWidth > 480 ? currValue2(10, 30, windowWidth) : "6rem",
          }}
        >
          {windowWidth > 1024
            ? [
                <KTFilterBaritem
                  title="Sort by popularity"
                  isSelected={true}
                  isActive={true}
                />,
                <KTFilterBaritem
                  title="All Artists"
                  hiddenArrow={true}
                  badgeValue="44"
                />,
                <KTFilterBaritem title="All Locations" />,
                <KTFilterBaritem title="Date Range" />,
                <KTFilterBaritem title="Select Venue" />,
              ]
            : [
                <KTFilterBaritem
                  title="Sort by popularity"
                  isSelected={true}
                  isActive={true}
                  hiddenArrow={true}
                />,
                <KTFilterBaritem
                  title="Sort by date"
                  isActive={true}
                  hiddenArrow={true}
                />,
              ]}
        </LeftContentWrapper>
        <RightContentWrapper>
          {windowWidth > 1024 ? (
            <div className="resetFilter">Reset Filters</div>
          ) : (
            <KTFilterButton onClick={onClickFilterItem} />
          )}
        </RightContentWrapper>
      </MusicsFilterBarWrapper>
    );
  }
);

export default KTMusicsFilterBar;
