import React, { memo, useState } from "react";
import { PromoCardWrappler } from "./style";

const KTPromoCard = memo((props) => {
  const [isOnMouseOver, setIsOnMouseOver] = useState(false);

  const { item, itemWidth, itemHeight, isSmall } = props;

  function onMouseOver() {
    setIsOnMouseOver(true);
  }

  function onMouseOut() {
    setIsOnMouseOver(false);
  }

  const srcName = isSmall
    ? isOnMouseOver
      ? item.smallImageHoverName
      : item.smallImageName
    : isOnMouseOver
    ? item.imageHoverName
    : item.imageName;
  return (
    <PromoCardWrappler
      style={{
        width: itemWidth,
        height: itemHeight,
        backgroundRepeat: "no-repeat",
        backgroundSize: "cover",
        backgroundImage:
          "url(" + require(`../../assets/img/promo-images/${srcName}`) + ")",
      }}
      onMouseOver={() => onMouseOver()}
      onMouseOut={() => onMouseOut()}
    >
      {/* <img
        src={require(`../../assets/img/promo-images/${srcName}`)}
        alt=""
        onMouseOver={() => onMouseOver()}
        onMouseOut={() => onMouseOut()}
      ></img> */}
    </PromoCardWrappler>
  );
});

export default KTPromoCard;

