import React, { memo, useState } from "react";
import {
  FilterCotnentArtistsWrapper,
  FilterCotnentGridWrapper,
  FilterCotnentSelectdWrapper,
} from "./style";

import KTFilterArtistCell from "./c-components/filter-artist-cell";

const KTFilterCotnentArtists = memo(({ windowWidth, artists = [] }) => {
  const [selectArtustIds, setSelectArtustIds] = useState([]);

  function onClickArtistCell(item) {
    if (selectArtustIds.includes(item.id)) {
      const newids = selectArtustIds.filter((id) => id !== item.id);
      setSelectArtustIds(newids);
    } else {
      setSelectArtustIds([item.id, ...selectArtustIds]);
    }
  }

  function onClickSelectAll() {
    const isSelectedAll = selectArtustIds.length === artists.length;
    if (isSelectedAll) {
      setSelectArtustIds([]);
    } else {
      setSelectArtustIds(artists.map((element) => element.id));
    }
  }

  const slides = artists.map((item) => {
    return (
      <KTFilterArtistCell
        artist={item}
        key={item.id}
        isSelected={selectArtustIds.includes(item.id)}
        onClick={onClickArtistCell}
      />
    );
  });

  const isSelectedAll = selectArtustIds.length === artists.length;

  return (
    <FilterCotnentArtistsWrapper>
      <div className="title">Artists</div>
      <div className="titleBottomLine" />
      <FilterCotnentSelectdWrapper
        selectAll={isSelectedAll}
        onClick={onClickSelectAll}
      >
        {isSelectedAll ? "Unselect All" : "Select All"}
      </FilterCotnentSelectdWrapper>
      <FilterCotnentGridWrapper>{slides}</FilterCotnentGridWrapper>
    </FilterCotnentArtistsWrapper>
  );
});

export default KTFilterCotnentArtists;
