import React, { memo } from "react";
import KTExpandableButton from "@/components/expandable-button";
import { Dropdown } from "antd";
import { HomeFilterDropdownItem } from "../style";

const KTHomeFilterCategoriesButton = memo(
  ({ buttonHeight, buttonFontSize }) => {
    const switchItems = [
      "All Categories",
      "NFL",
      "NCAA W",
      "High School Football",
      "NCAA BB",
      "NCAA F",
    ];

    const items = switchItems
      .map((title, index, array) => {
        const resultArr = [
          {
            label: (
              <HomeFilterDropdownItem className="title">
                {title}
              </HomeFilterDropdownItem>
            ),
            key: index,
          },
        ];

        if (index < array.length - 1) {
          resultArr.push({
            type: "divider",
            style: { margin: "0 30px" },
          });
        }

        return resultArr;
      })
      .flat();

    return (
      <Dropdown
        menu={{ items }}
        trigger={["click"]}
        overlayStyle={{
          borderRadius: "20px",
          overflow: "hidden",
          border: "1px solid #8C8C8C",
          background: "#FFF",
        }}
      >
        <KTExpandableButton
          placement="bottomLeft"
          title={"All Categories"}
          buttonHeight={buttonHeight}
          buttonFontSize={buttonFontSize}
          onClick={(e) => e.preventDefault()}
        />
      </Dropdown>
    );
  }
);

export default KTHomeFilterCategoriesButton;
