import React, { memo, useState } from "react";

import { HomeFilterBarWrapper } from "./style";
import KTHomeFilterSortButton from "./buttons/home-filter-sort-button";
import KTHomeFilterCategoriesButton from "./buttons/home-filter-categories-button";
import KTHomeFilterDateButton from "./buttons/home-filter-date-button";
import withWindowSize from "@/components/higher-order/withWindowSize";
import KTHomeFilterVenuesButton from "./buttons/home-filter-venues-button";
import classNames from "classnames";
import { ReactComponent as FilterIcon } from "@/assets/img/icons/icon-filter.svg";

import { Drawer } from "antd";
import KTFilterContent from "../filter-content";

const KTHomeFilterBar = memo(({ windowSize }) => {
  const [filterViewOpen, setFilterViewOpen] = useState(false);

  function calculateSize(windowSize) {
    const windowWidth = windowSize.width;
    if (windowWidth >= 1920) {
      return {
        wrapperPaddingLeftRight: "100px",
        wrapperHeight: "120px",
        buttonHeight: "40px",
        buttonFontSize: "15px",
        leftWrapperGap: "30px",
      };
    } else if (windowWidth <= 480) {
      return {
        wrapperPaddingLeftRight: "20rem",
        wrapperHeight: "76rem",
        buttonHeight: "28rem",
        buttonFontSize: "12rem",
        leftWrapperGap: "20rem",
      };
    } else if (windowWidth <= 1024) {
      return {
        wrapperPaddingLeftRight: "50px",
        wrapperHeight: "100px",
        buttonHeight: "34px",
        buttonFontSize: "12px",
        leftWrapperGap: "20px",
      };
    } else {
      const ratio = (windowWidth - 1024) / (1920 - 1024);

      const wrapperPaddingLeftRight = ratio * 50 + 50;
      const wrapperHeight = ratio * 20 + 100;
      const buttonHeight = ratio * 6 + 34;
      const buttonFontSize = ratio * 3 + 12;
      const leftWrapperGap = ratio * 10 + 20;

      return {
        wrapperPaddingLeftRight: `${wrapperPaddingLeftRight}px`,
        wrapperHeight: `${wrapperHeight}px`,
        buttonHeight: `${buttonHeight}px`,
        buttonFontSize: `${buttonFontSize}px`,
        leftWrapperGap: `${leftWrapperGap}px`,
      };
    }
  }

  const {
    wrapperPaddingLeftRight,
    wrapperHeight,
    buttonHeight,
    buttonFontSize,
    leftWrapperGap,
  } = calculateSize(windowSize);

  function getFilterBarChildren(windowSize) {
    const windowWidth = windowSize.width;
    if (windowWidth > 1024) {
      return [
        <div className="filterLeft" style={{ gap: leftWrapperGap }}>
          <KTHomeFilterSortButton
            buttonHeight={buttonHeight}
            buttonFontSize={buttonFontSize}
          />

          <div className="line"></div>
          <KTHomeFilterCategoriesButton
            buttonHeight={buttonHeight}
            buttonFontSize={buttonFontSize}
          />
          <KTHomeFilterDateButton
            buttonHeight={buttonHeight}
            buttonFontSize={buttonFontSize}
          />
          <KTHomeFilterVenuesButton
            buttonHeight={buttonHeight}
            buttonFontSize={buttonFontSize}
          />
        </div>,
        <div className="resetButton">Reset Filters</div>,
      ];
    } else {
      return [
        <div className="filterLeft" style={{ gap: leftWrapperGap }}>
          <div className={classNames("sortButton", { sortButtonActive: true })}>
            Sort by popularity
          </div>
          <div className={classNames("sortButton", {})}>Sort by date</div>
        </div>,
        <FilterIcon
          className="funnel"
          onClick={() => setFilterViewOpen(true)}
        />,
        // funnelSvg({ className: "funnel" }),
      ];
    }
  }

  return (
    <HomeFilterBarWrapper
      style={{
        padding: `0 ${wrapperPaddingLeftRight}`,
        height: wrapperHeight,
      }}
    >
      {getFilterBarChildren(windowSize)}
      <Drawer
        closeIcon={null}
        placement="bottom"
        height={
          windowSize.width > 480
            ? `${window.innerHeight - 80}px`
            : `${window.innerHeight - 59}rem`
        }
        open={filterViewOpen}
        onClose={() => setFilterViewOpen(false)}
        style={{
          borderRadius:
            windowSize.width > 480 ? "20px 20px 0 0" : "20rem 20rem 0 0",
          background: "#FFF",
        }}
        bodyStyle={{ padding: "0" }}
      >
        <KTFilterContent onClose={() => setFilterViewOpen(false)} />
      </Drawer>
    </HomeFilterBarWrapper>
  );
});

export default withWindowSize(KTHomeFilterBar);
