import React, { memo } from "react";
import {
  FilterArtistCellWrapper,
  FilterArtistNameWrapper,
  FilterArtistPicWrapper,
} from "./style";

const KTFilterArtistCell = memo(({ artist, isSelected = false, onClick }) => {
  return (
    <FilterArtistCellWrapper onClick={() => onClick(artist)}>
      <FilterArtistPicWrapper
        style={{
          backgroundImage: isSelected
            ? "url(" +
              require(`@/assets/img/icons/icon-artist-selected.png`) +
              ")"
            : "url(" +
              require(`@/assets/img/temporary/${artist.picName}`) +
              ")",
        }}
      />
      <FilterArtistNameWrapper>{artist.name}</FilterArtistNameWrapper>
    </FilterArtistCellWrapper>
  );
});

export default KTFilterArtistCell;
