import { Dropdown } from "antd";
import React, { memo } from "react";
import KTExpandableButton from "@/components/expandable-button";
import { HomeFilterDropdownItem } from "../style";

const KTHomeFilterSortButton = memo(({ buttonHeight, buttonFontSize }) => {
  const switchItems = ["Sort by date", "Sort by popularity"];
  const items = switchItems
    .map((title, index, array) => {
      const resultArr = [
        {
          label: (
            <HomeFilterDropdownItem className="title">
              {title}
            </HomeFilterDropdownItem>
          ),
          key: index,
        },
      ];

      if (index < array.length - 1) {
        resultArr.push({
          type: "divider",
          style: { margin: "0 30px" },
        });
      }

      return resultArr;
    })
    .flat();

  return (
    <Dropdown
      menu={{ items }}
      trigger={["click"]}
      overlayStyle={{
        borderRadius: "20px",
        overflow: "hidden",
        border: "1px solid #8C8C8C",
        background: "#FFF",
      }}
    >
      <KTExpandableButton
        placement="bottomLeft"
        title={"Sort by popularity"}
        buttonHeight={buttonHeight}
        buttonFontSize={buttonFontSize}
        onClick={(e) => e.preventDefault()}
      ></KTExpandableButton>
    </Dropdown>
  );
});

export default KTHomeFilterSortButton;
