import React, { memo } from "react";
import { ExpandableButtonWapper } from "./style";
import { arrowSvg } from "../../common/icon-svg";

const KTExpandableButton = memo(
  ({ title, onClick, buttonHeight, buttonFontSize }) => {
    return (
      <ExpandableButtonWapper
        onClick={onClick}
        style={{ height: buttonHeight }}
      >
        <div className="title" style={{ fontSize: buttonFontSize }}>
          {title}
        </div>
        {arrowSvg({
          width: "12px",
          height: "6px",
          fill: "#4D4D4D",
        })}
      </ExpandableButtonWapper>
    );
  }
);

export default KTExpandableButton;
