import React, { memo } from "react";
import KTExpandableButton from "@/components/expandable-button";
import { Dropdown } from "antd";
import { HomeFilterDropdownItem } from "../style";

const KTHomeFilterVenuesButton = memo(({ buttonHeight, buttonFontSize }) => {
  const switchItems = [
    "All Venue",
    "1900 Building",
    "23rd St Roadhouse",
    "7th Street Event Center",
    "Art Salon Ku",
    "Arvest Bank Theatre at The Midland",
    "Arvest Bank Theatre at The MidLand Parking Lots",
  ];

  const items = switchItems
    .map((title, index, array) => {
      const resultArr = [
        {
          label: (
            <HomeFilterDropdownItem className="title">
              {title}
            </HomeFilterDropdownItem>
          ),
          key: index,
        },
      ];

      if (index < array.length - 1) {
        resultArr.push({
          type: "divider",
          style: { margin: "0 30px" },
          // label: <HomeFilterDropdownDivider />,
        });
      }

      return resultArr;
    })
    .flat();

  return (
    <Dropdown
      menu={{ items }}
      trigger={["click"]}
      overlayStyle={{
        borderRadius: "20px",
        overflow: "hidden",
        border: "1px solid #8C8C8C",
        background: "#FFF",
      }}
    >
      <KTExpandableButton
        placement="bottomLeft"
        title={"All Venues"}
        buttonHeight={buttonHeight}
        buttonFontSize={buttonFontSize}
        onClick={(e) => e.preventDefault()}
      ></KTExpandableButton>
    </Dropdown>
  );
});

export default KTHomeFilterVenuesButton;
