import React, { memo } from "react";
import { NavLink } from "react-router-dom";

import {
  AppHeaderWrapper,
  AppHeaderLeft,
  AppHeaderRight,
  AppHeaderLeftSearchBar,
  AppHeaderSearchBarOperableWrapper,
} from "./style";

import { headerLinks } from "@/common/local-data";
import {
  profileHeaderSvg,
  appHeaderSearchButtonSvg,
  addressSvg,
  calendarSvg,
  dividerHSvg,
  menuSvg,
} from "../../common/icon-svg";
import { Input } from "antd";
import withWindowSize from "../higher-order/withWindowSize";
import { ReactComponent as LogoIcon } from "@/assets/img/svg/icon-nav-logo.svg";

const KTAppHeader = memo(({ windowSize }) => {
  const showSelectItem = (item) => {
    return (
      <NavLink activeClassName="menu-item-active" to={item.link}>
        {item.title}
      </NavLink>
    );
  };

  function getSearchBarChildren(windowSize) {
    const windowWidth = windowSize.width;

    if (windowWidth > 1280) {
      return [
        <AppHeaderSearchBarOperableWrapper key={"addressOperable"}>
          {addressSvg({
            className: "icon",
          })}
          <div className="title">Los Angeles, CA</div>
        </AppHeaderSearchBarOperableWrapper>,
        dividerHSvg({ className: "divider" }),
        <AppHeaderSearchBarOperableWrapper key={"calendarOperable"}>
          {calendarSvg({
            className: "icon",
          })}
          <div className="title">Oct. 02 - Oct. 31</div>
        </AppHeaderSearchBarOperableWrapper>,
        dividerHSvg({ className: "divider" }),
        <Input
          placeholder="Search Events, Performers and Venues..."
          bordered={false}
          className="input"
        ></Input>,
        appHeaderSearchButtonSvg({
          className: "searchButton",
        }),
      ];
    }
    return [
      <Input
        placeholder="Search Events, Performers and Venues..."
        bordered={false}
        className="input"
      ></Input>,
      appHeaderSearchButtonSvg({
        className: "searchButton",
      }),
    ];
  }

  function getSearchBarWidth(windowSize) {
    const windowWidth = windowSize.width;
    return windowWidth > 1280 ? "660px" : "350px";
  }

  function getAppHeaderSearchBar(windowSize) {
    return [
      <AppHeaderLeftSearchBar
        style={{
          width: getSearchBarWidth(windowSize),
        }}
      >
        {getSearchBarChildren(windowSize)}
      </AppHeaderLeftSearchBar>,
    ];
  }

  function getAppHeaderChildren(windowSize) {
    const windowWidth = windowSize.width;

    if (windowWidth > 1024) {
      return [
        <AppHeaderLeft>
          <NavLink to="/" className="logoNavlink" key={"Logo"}>
            <LogoIcon className="logoNav" />
          </NavLink>
          {getAppHeaderSearchBar(windowSize)}
        </AppHeaderLeft>,
        <AppHeaderRight>
          <div className="menu-item-wrapper">
            {headerLinks.map((item, index) => {
              return (
                <div key={item.title} className="menu-item">
                  {showSelectItem(item, index)}
                </div>
              );
            })}
          </div>
          {profileHeaderSvg({
            className: "user-header",
          })}
        </AppHeaderRight>,
      ];
    } else if (windowWidth > 601) {
      return [
        <NavLink to="/" key={"miniLogo"}>
          <LogoIcon className="miniLogo" />
        </NavLink>,
        getAppHeaderSearchBar(windowSize),
        menuSvg({ className: "menu" }),
      ];
    } else {
      return [
        getAppHeaderSearchBar(windowSize),
        menuSvg({ className: "menu" }),
      ];
    }
  }

  return (
    <AppHeaderWrapper>{getAppHeaderChildren(windowSize)}</AppHeaderWrapper>
  );
});

export default withWindowSize(KTAppHeader);
