import React, { memo, useState, useEffect } from "react";
import {
  BackgroundImageWrapper,
  ContentDateWrapper,
  ContentInfoWrapper,
  HumanoidSignWrapper,
  LeftWrapper,
  MusicsHeaderWrapper,
  RightWrapper,
} from "./style";
import KTMusicssHeaderList from "../musics-header-list";
import { musicHeaderConcerts } from "@/common/music-ephemeral-data";
import { currValue, currValueOfRange } from "@/utils";
import withWindowSize from "@/components/higher-order/withWindowSize";
import KTMusicsViewAll from "../musics-view-all";

const KTMusicsHeader = memo(({ windowSize }) => {
  const [selectedIndex, setSelectedIndex] = useState(0);
  const concerts = musicHeaderConcerts;

  useEffect(() => {
    const timer = setTimeout(() => {
      if (selectedIndex + 1 >= concerts.length) {
        setSelectedIndex(0);
      } else {
        setSelectedIndex(selectedIndex + 1);
      }
    }, 1000 * 4);

    return () => {
      clearTimeout(timer);
    };
  }, [selectedIndex, concerts]);

  function onclickConcert(concert, index) {
    setSelectedIndex(index);
  }

  const windowWidth = windowSize.width;

  return (
    <MusicsHeaderWrapper>
      <BackgroundImageWrapper
        style={{
          backgroundImage:
            "url(" + require(`@/assets/img/music/music-header-bg.jpg`) + ")",
        }}
      />
      <HumanoidSignWrapper
        style={{
          backgroundImage:
            "url(" +
            require(`@/assets/img/temporary/${concerts[selectedIndex].humanoidSign}`) +
            ")",
          right: currValue(0, 110, windowWidth),
        }}
      />
      <LeftWrapper>
        <KTMusicssHeaderList
          selectedIndex={selectedIndex}
          concerts={concerts}
          onclickConcert={onclickConcert}
          windowWidth={windowWidth}
        />
      </LeftWrapper>
      <RightWrapper
        style={{
          marginLeft: currValueOfRange(20, 257, windowWidth, 481, 1920),
        }}
      >
        <ContentInfoWrapper>
          <div
            className="name"
            style={{
              fontSize:
                windowWidth > 480 ? currValue(70, 110, windowWidth) : "40rem",
            }}
          >
            {concerts[selectedIndex].name}
          </div>
          <div className="nameLine" />
          <div className="eventTitle">{concerts[selectedIndex].title}</div>
          {concerts[selectedIndex].formDate === undefined ? null : (
            <ContentDateWrapper
              style={{
                marginTop: windowWidth > 480 ? "30px" : "10rem",
              }}
            >
              <div className="title">FROM</div>
              <div className="detaile">{concerts[selectedIndex].formDate}</div>
            </ContentDateWrapper>
          )}
          {concerts[selectedIndex].toDate === undefined ? null : (
            <ContentDateWrapper
              style={{
                marginTop: windowWidth > 480 ? "10px" : "0",
              }}
            >
              <div className="title">TO</div>
              <div className="detaile">{concerts[selectedIndex].toDate}</div>
            </ContentDateWrapper>
          )}
        </ContentInfoWrapper>
        <KTMusicsViewAll />
      </RightWrapper>
    </MusicsHeaderWrapper>
  );
});

export default withWindowSize(KTMusicsHeader);
