import React, { memo } from "react";
import {
  HeadPortraitItemMarkWrapper,
  HeadPortraitItemWrapper,
  MusicssHeaderListWrapper,
} from "./style";
import { Swiper, SwiperSlide } from "swiper/react";

const KTMusicssHeaderList = memo(
  ({ concerts = [], selectedIndex, onclickConcert, windowWidth }) => {
    const slides = concerts.map((concert, index) => {
      return (
        <SwiperSlide
          className="swiperSlide"
          onClick={() => onclickConcert(concert, index)}
        >
          <HeadPortraitItemWrapper
            style={{
              backgroundImage:
                "url(" +
                require(`@/assets/img/temporary/${concert.headPortrait}`) +
                ")",
            }}
          >
            {selectedIndex === index ? null : <HeadPortraitItemMarkWrapper />}
          </HeadPortraitItemWrapper>
        </SwiperSlide>
      );
    });

    return (
      <MusicssHeaderListWrapper>
        <Swiper
          slidesPerView={"auto"}
          spaceBetween={windowWidth > 480 ? "30px" : "20rem"}
          direction="vertical"
        >
          {slides}
        </Swiper>
      </MusicssHeaderListWrapper>
    );
  }
);

export default KTMusicssHeaderList;
