import React, { memo } from "react";
import {
  FilterContentDateNodeWrapper,
  FilterContentDateRangeCalendarWrapper,
  FilterContentDateRangeHeaderWrapper,
  FilterContentDateRangeListWrapper,
  FilterContentDateRangeWrapper,
} from "./style";
import { homeAllFilterDateRange } from "@/common/ephemeral-data";
import { useState } from "react";
import classNames from "classnames";
import { DayPicker } from "react-day-picker";
import "react-day-picker/dist/style.css";
import "@/assets/css/custom-daypicker.css";

const pastMonth = new Date();

const KTFilterContentDateRange = memo(() => {
  const [selectedRange, setSelectedRange] = useState(
    homeAllFilterDateRange[0].title
  );

  const [pickerRange, setPickerRange] = useState(undefined);

  function onclickRange(item) {
    setSelectedRange(item.title);
  }

  function dateRangeItems() {
    return homeAllFilterDateRange.map((item) => {
      return (
        <div
          key={item.title}
          className={classNames("item", {
            itemSelected: selectedRange === item.title,
          })}
          onClick={() => onclickRange(item)}
        >
          {item.title}
        </div>
      );
    });
  }

  function dateNodeTitle(date) {
    if (date === undefined) {
      return "";
    }
    const options = { month: "short", day: "numeric", year: "numeric" };
    const formattedDate = date.toLocaleDateString("en-US", options);
    return formattedDate;
  }

  function dateRangeCalendar() {
    return selectedRange ===
      homeAllFilterDateRange[homeAllFilterDateRange.length - 1].title ? (
      <FilterContentDateRangeCalendarWrapper>
        <div className="headerWrapper">
          <div className="headerDateNodeWrapper">
            <FilterContentDateNodeWrapper>
              <div className="title">From</div>
              <div className="content">
                {pickerRange && pickerRange.from
                  ? dateNodeTitle(pickerRange.from)
                  : ""}
              </div>
            </FilterContentDateNodeWrapper>
            <FilterContentDateNodeWrapper>
              <div className="title">To</div>
              <div className="content">
                {pickerRange && pickerRange.to
                  ? dateNodeTitle(pickerRange.to)
                  : ""}
              </div>
            </FilterContentDateNodeWrapper>
          </div>
        </div>
        <DayPicker
          id="home-all-filter-daterange"
          mode="range"
          defaultMonth={pastMonth}
          selected={pickerRange}
          onSelect={(range) => {
            
            setPickerRange(range);
          }}
        />
      </FilterContentDateRangeCalendarWrapper>
    ) : null;
  }
  return (
    <FilterContentDateRangeWrapper>
      <FilterContentDateRangeHeaderWrapper>
        <div className="title">Date Range</div>
        <div className="divider" />
      </FilterContentDateRangeHeaderWrapper>
      <FilterContentDateRangeListWrapper>
        {dateRangeItems()}
      </FilterContentDateRangeListWrapper>
      {dateRangeCalendar()}
    </FilterContentDateRangeWrapper>
  );
});

export default KTFilterContentDateRange;
